<template>
  <div>
    <h1>Full Name : {{ fullname }}</h1>
  </div>
</template>
<script>
export default {
  data() {
    return {
      firstName: "inseo",
      lastName: "jeong",
      fullname: "",
    };
  },
  watch: {
    firstName() {
      this.fullName = this.firstName + "" + this.lastName;
    },
    lastName() {
      this.fullName = this.firstName + "" + this.lastName;
    },
  },
};
</script>
<style></style>
